div {
  box-sizing: border-box;
}

.container {
  display: flex;
}

.leftContent {
  flex: 1;
  height: 100vh;
  overflow-y: auto;
  padding: 15px 5px;
}

.rightNav {
  width: 100px;
  height: 100vh;
  overflow-y: auto;
  padding: 15px 5px;
}

.rightNav > div {
  width: 100%;
}

.wrapAnchor {
  margin-top: 10px;
  height: calc(100vh - 130px);
  overflow-y: auto;
}

.pointer {
  cursor: pointer;
}
